import {INCREMENT,DECREMENT} from './types'
import getters from './getters'

const state = {
    count:20
};

const mutations = {
    [INCREMENT](){
        state.count++;
    },
    [DECREMENT](){
        state.count--;
    }
}

export default {
    state,
    mutations,
    getters
}


