<template>
    <div class="vuex">
        <input type="button" value="增加" @click="increment" />
        <input type="button" value="减少" @click="decrement" />
        <div>现在数字为：{{count}}</div>
        <div @click="goPage2">跳转到page2</div>
    </div>
</template>

<script>
    import {mapGetters,mapActions} from 'vuex'
    export default {
        data() {
            return {

            }
        },
        computed:mapGetters([
            'count'
        ]),
        /*methods:mapActions([
            'increment',
            'decrement',
        ])*/
        methods:{
            ...mapActions([
                'increment',
                'decrement',
            ]),
            goPage2(){
                this.$router.push({name:'Page2'});
            }
        }
    }
</script>
<style scoped>

</style>



