<template>
    <div class="hello">
        <h1>{{ msg }}</h1>
        <div><input /></div>
        <input type="button" value="跳转" @click="goTo" />
    </div>
</template>

<script>
    export default {
        data() {
            return {
                msg: 'Hello world'
            }
        },
        methods:{
            goTo(){
                this.$router.push({name:'Page1'});
            }
        }
    }
</script>
<style scoped>

</style>



