<template>
    <div class="hello">
        <h1>{{ msg }}</h1>
        <div><input /></div>
        <input type="button" value="跳转" @click="goTo" />
    </div>
</template>

<script>
    export default {
        name:'page1',
        data() {
            return {
                msg: 'Page1'
            }
        },
        methods:{
            goTo(){
                this.$router.push({name:'Page2'});
            }
        },
        beforeRouteLeave (to, from, next) {
            // 导航离开该组件的对应路由时调用
            // 可以访问组件实例 `this`
            console.log(to);
        }
    }
</script>
<style scoped>

</style>



