<template>
    <div class="hello">
        <h1>{{ msg }}</h1>
        <div><input /></div>
        <div>count值为：{{count}}</div>
        <input type="button" value="跳转" @click="goTo" />
    </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    export default {
        name:'page2',
        data() {
            return {
                msg: 'Page2'
            }
        },
        computed:{
            ...mapGetters([
                'count'
            ])
        },
        methods:{
            goTo(){
                this.$router.push({name:'Page1'});
            }
        }
    }
</script>
<style scoped>

</style>



